'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

interface HoroscopeResponse {
  text: string;
  type: 'daily' | 'weekly';
  weekDates?: {
    start: string;
    end: string;
  };
}

export default function HoroscopePage() {
  const router = useRouter()
  const [sign, setSign] = useState<string>('')
  const [loading, setLoading] = useState(false)
  const [horoscope, setHoroscope] = useState<HoroscopeResponse | null>(null)
  const [mounted, setMounted] = useState(false)

  useEffect(() => {
    setMounted(true)
  }, [])

  useEffect(() => {
    if (mounted) {
      const savedSign = localStorage.getItem('astrohoroscope-sign')
      if (!savedSign) {
        router.replace('/')
        return
      }
      setSign(savedSign)
    }
  }, [mounted, router])

  const getWeekDates = () => {
    const today = new Date()
    const currentDay = today.getDay() // 0 = Sunday, 1 = Monday, etc.
    
    // Find last Sunday
    const lastSunday = new Date(today)
    lastSunday.setDate(today.getDate() - currentDay)
    
    // Find next Sunday
    const nextSunday = new Date(lastSunday)
    nextSunday.setDate(lastSunday.getDate() + 7)
    
    // Format dates as DD/MM/YYYY
    const formatDate = (date: Date) => {
      return date.toLocaleDateString('pt-BR', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric'
      })
    }

    return {
      start: formatDate(lastSunday),
      end: formatDate(nextSunday)
    }
  }

  const generateHoroscope = async (type: 'daily' | 'weekly') => {
    if (!sign) return

    setLoading(true)
    try {
      // Simulating API call with mock data
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      const mockHoroscopes = {
        daily: `Hoje é um dia especial para ${sign}. As estrelas indicam um momento de reflexão e crescimento pessoal. Aproveite para meditar sobre seus objetivos e fazer planos para o futuro. A energia cósmica está favorável para novos começos.`,
        weekly: `Esta semana será transformadora para ${sign}. Os astros sugerem mudanças positivas em diferentes aspectos da sua vida. Mantenha-se aberto a novas oportunidades e confie em sua intuição. Um ciclo importante está se iniciando.`
      }

      const horoscopeText = type === 'daily' ? mockHoroscopes.daily : mockHoroscopes.weekly
      
      const response: HoroscopeResponse = {
        text: horoscopeText,
        type,
        ...(type === 'weekly' && { weekDates: getWeekDates() })
      }

      setHoroscope(response)

      // Store in localStorage with expiry
      localStorage.setItem(`astrohoroscope-${sign}-${type}`, JSON.stringify({
        ...response,
        expiry: type === 'daily' 
          ? new Date().setHours(23, 59, 59, 999)
          : new Date().setDate(new Date().getDate() + 7)
      }))
    } catch (error) {
      console.error('Error generating horoscope:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleChangeSign = () => {
    localStorage.removeItem('astrohoroscope-sign')
    router.replace('/')
  }

  if (!mounted || !sign) {
    return null
  }

  return (
    <main className="min-h-screen bg-gradient-to-br from-slate-900 to-slate-800 text-white p-8">
      <div className="max-w-4xl mx-auto">
        <div className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl font-bold mb-4 font-space-grotesk">
            {sign}
          </h1>
          <p className="text-gray-300 text-lg">
            Descubra o que os astros reservam para você
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
          <Button
            className="bg-indigo-600 hover:bg-indigo-700 h-16 text-lg"
            onClick={() => generateHoroscope('daily')}
            disabled={loading}
          >
            Horóscopo do Dia
          </Button>
          <Button
            className="bg-purple-600 hover:bg-purple-700 h-16 text-lg"
            onClick={() => generateHoroscope('weekly')}
            disabled={loading}
          >
            Horóscopo da Semana
          </Button>
          <Button
            className="bg-emerald-600 hover:bg-emerald-700 h-16 text-lg"
            onClick={() => router.replace(`/characteristics/${sign}`)}
            disabled={loading}
          >
            🌟 Características
          </Button>
        </div>

        {loading && (
          <Card className="p-8 bg-slate-800/50 border-slate-700 text-center">
            <p className="text-lg">Consultando os astros...</p>
          </Card>
        )}

        {!loading && horoscope && (
          <Card className="p-8 bg-slate-800/50 border-slate-700">
            <h2 className="text-2xl font-semibold mb-4">
              {horoscope.type === 'daily' ? 'Horóscopo do Dia' : (
                <>
                  Horóscopo da Semana
                  {horoscope.weekDates && (
                    <div className="text-sm text-gray-400 mt-1">
                      {horoscope.weekDates.start} - {horoscope.weekDates.end}
                    </div>
                  )}
                </>
              )}
            </h2>
            <p className="text-gray-300 text-lg leading-relaxed">
              {horoscope.text}
            </p>
          </Card>
        )}

        <div className="text-center mt-8">
          <Button
            variant="outline"
            className="border-gray-600 text-gray-300 hover:bg-slate-700"
            onClick={handleChangeSign}
          >
            Mudar de Signo
          </Button>
        </div>
      </div>
    </main>
  )
}
